html {
  font-size: 62.5%;
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  background-color: #F2F4F8;
}

h1, h2, h3, h4 {
  margin: 0;
  color: #1F3E72;
}

img {
  max-width: 100%;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

p {
  font-size: 2rem;
  color: #9498A5;
}

li {
  font-size: 2rem;
  color: #9498A5;
}

a {
  text-decoration: none;
  color: #9498A5;
  font-size: 2rem;
}

.container {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto;
}

.no-margin {
  margin: 0;
}

.center-text {
  text-align: center;
}

.no-padding {
  padding: 0;
}

.nav-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 10rem;
}
.nav-container .logo {
  color: #0829B1;
  font-size: 2.5rem;
}
.nav-container .nav-container__btns {
  display: flex;
  align-items: center;
  gap: 2.5rem;
}
.nav-container .nav-container__btns .nav__btn {
  font-weight: 500;
}
.nav-container .nav-container__btns .nav__btn:last-of-type {
  font-weight: 500;
  letter-spacing: 0.05rem;
}

.nav__btn--blue {
  box-shadow: 0px 5px 13px 0px rgba(8, 41, 177, 0.5);
  color: #ffffff;
  padding: 1.5rem 3.3rem 1.5rem 3.3rem;
  background-color: #164DBC;
  border-radius: 0.5rem;
  transition: all 100ms ease-in-out;
}
.nav__btn--blue:hover {
  background-color: #164DBC;
  cursor: pointer;
}

.hero {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.hero .hero__img {
  max-width: 100%;
  background-image: url("../img/1.jpg");
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 67rem;
}
.hero .hero__info {
  width: 90%;
  display: flex;
  flex-direction: column;
  row-gap: 2.5rem;
  margin-top: 5rem;
}
.hero .hero__info .hero__info-text {
  line-height: 3.5rem;
}

.hero__info-1 {
  width: 100%;
  display: flex;
  column-gap: 5rem;
}
.hero__info-1 .icon-container {
  background-color: #FFF9E4ff;
  padding: 0.2rem;
  width: 2.5rem;
  height: 2.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 0.5rem;
  box-shadow: 0px 5px 3px 0px rgba(255, 249, 228, 0.9);
}
.hero__info-1 .icon-container .icon1 {
  background-image: url("../img/shield.png");
  background-size: cover;
  width: 1.6rem;
  height: 1.6rem;
}
.hero__info-1 .icon-container .icon2 {
  background-image: url("../img/star.png");
  background-size: cover;
  width: 1.6rem;
  height: 1.6rem;
}
.hero__info-1 .hero__info-1--1 {
  display: flex;
  align-items: center;
  column-gap: 1rem;
  margin-top: 2.5rem;
}
.hero__info-1 .hero__info-1--1 p {
  color: #1F3E72;
  font-weight: 500;
}

.hero__info-2 {
  margin-top: 8rem;
  width: 100%;
  display: flex;
  column-gap: 5rem;
}
.hero__info-2 .icon-container {
  background-color: #164dbc1e;
  padding: 0.2rem;
  width: 2.5rem;
  height: 2.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 0.5rem;
  box-shadow: 0px 5px 3px 0px rgba(8, 41, 177, 0.2);
}
.hero__info-2 .icon-container .icon3 {
  background-image: url("../img/clock.png");
  background-size: cover;
  width: 1.6rem;
  height: 1.6rem;
}

.hero__info-1--2 {
  display: flex;
  align-items: center;
  column-gap: 1rem;
  margin-top: 2.5rem;
}
.hero__info-1--2 p {
  color: #1F3E72;
  font-weight: 500;
}

.hero__date {
  background-color: #F2F4F8;
  padding: 1.6666666667rem 2.5rem 1.6666666667rem 2.5rem;
  border-radius: 0.5rem;
  position: absolute;
  width: 90rem;
  bottom: 5rem;
  right: 45rem;
  display: flex;
  align-items: center;
  column-gap: 4rem;
  box-shadow: 0px 5px 30px 0px rgba(8, 41, 177, 0.3);
}
.hero__date .location {
  width: 100%;
  display: flex;
  column-gap: 5rem;
}
.hero__date .date {
  width: 100%;
  display: flex;
  column-gap: 5rem;
}
.hero__date .type {
  width: 100%;
  display: flex;
  column-gap: 5rem;
}

.hero__date-title {
  display: flex;
  flex-direction: column;
  row-gap: 0.5rem;
}
.hero__date-title .info {
  margin-left: 3.5rem;
  font-weight: 500;
  color: #1F3E72;
}

.hero__date-title--container {
  display: flex;
  align-items: center;
  column-gap: 1rem;
}
.hero__date-title--container .icon-container {
  background-color: #164dbc1e;
  padding: 0.2rem;
  width: 2.5rem;
  height: 2.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 0.5rem;
}
.hero__date-title--container .icon-container .icon4 {
  background-image: url("../img/map.png");
  background-size: cover;
  width: 1.6rem;
  height: 1.6rem;
}
.hero__date-title--container .icon-container .icon5 {
  background-image: url("../img/date.png");
  background-size: cover;
  width: 1.6rem;
  height: 1.6rem;
}
.hero__date-title--container .icon-container .icon6 {
  background-image: url("../img/pencil.png");
  background-size: cover;
  width: 1.6rem;
  height: 1.6rem;
}